.container {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__minutes-hand {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 600s steps(60, end) infinite rotate;
  position: absolute;
  top: 20vmin;
  left: 38.5vmin;
}

.stopwatch__seconds-hand {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 10s linear infinite rotate;
  position: absolute;
  top: 2vmin;
  left: 39.25vmin;
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 10vmin;
  height: 10vmin;
  position: absolute;
  top: 35vmin;
  left: 35vmin;
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

